<template>
  <div v-loading="loading" class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="handleGoBack">返 回</el-button>
      </div>
      <div class="fr">
        <el-button :disabled="!([3, 4].includes(form.status) && form.canStockInMatarialCount > 0)" type="primary" @click="handleInWarehouse">入库</el-button>
      </div>
    </el-row>
    <!-- 基础交接单信息 -->
    <transition name="el-zoom-in-top">
      <el-row>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="left">
          <el-row :gutter="20">
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="交接单号" prop="no">
                <el-input
                  v-model.trim="form.no"
                  placeholder="系统自动生成"
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="合同号" prop="contractNo">
                <el-input
                  v-model.trim="form.contractNo"
                  placeholder="请填写合同号"
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="订单号" prop="orderNo">
                <el-input
                  v-model.trim="form.orderNo"
                  placeholder="请填写订单号"
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="供应商" prop="supplierName">
                <el-input
                  v-model.trim="form.supplierName"
                  placeholder="请填写供应商"
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="用料单位" prop="materialsUnit">
                <el-input
                  v-model.trim="form.materialsUnit"
                  placeholder="请填写用料单位"
                  style="width: 100%;"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="凭证日期" prop="deliveryDate">
                <el-date-picker
                  v-model="form.deliveryDate"
                  type="date"
                  style="width: 100%;"
                  value-format="yyyy-MM-dd"
                  placeholder="请选择凭证日期"
                  :clearable="false"
                  :editable="false"
                  disabled
                />
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="是否检验" prop="inspect">
                <!-- 交接单状态为待更新需要禁用(已驳回交接单的状态会变为待更新) -->
                <el-radio-group v-model="form.inspect" disabled style="width: 178px;" class="my-inspect-sty">
                  <el-radio-button :label="true">是</el-radio-button>
                  <el-radio-button :label="false">否</el-radio-button>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :xs="12" :sm="12" :md="12" :lg="8" :xl="8">
              <el-form-item label="备注" prop="remarks">
                <el-input v-model="form.remarks" disabled type="textarea" :rows="1" placeholder="请填写备注" />
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-row>
    </transition>
    <!-- 交接单物料信息 -->
    <el-divider content-position="left">交接单信息</el-divider>
    <el-row>
      <div class="clearfix top-filter">
        <el-form ref="filterItem" :model="query" inline>
          <el-form-item prop="code">
            <el-input v-model="query.code" placeholder="物料编码或名称" clearable style="width: 178px;" />
          </el-form-item>
          <el-button type="success" icon="el-icon-search" @click="searchInit">搜索</el-button>
          <el-button type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')">重置</el-button>
        </el-form>
      </div>
    </el-row>
    <!-- 交接单物料信息-列表区域(表格具有多选功能) -->
    <el-row>
      <table-com
        :ref="refTableName"
        :ref-name="'tableRef'"
        :table-height-type="450"
        :selection="false"
        :selection-fixed="false"
        :table-selection="tableSelection"
        :loading="tableLoading"
        :index="true"
        :table-column-sort="false"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="170"
        :expand-list="expandList"
        :show-operate="true"
        :cell-style="handleCellStyle"
        :refresh-key="refreshKey"
        :selectable="selectable"
        @rowClick="handleRowClick"
        @refresh="searchInit"
        @selectionChange="selectionChange"
        @dynamicRow="getDynamicRow"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleViewBarcode(scope.row)">查看条码</el-button>
        </template>
      </table-com>
    </el-row>

    <!-- 生成条码 -->
    <generate-barcode
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :extra-info="{}"
      @confirm="confirmCallBack"
    />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import tableHeadSet from '@/mixins/tableHeadSet'
import GenerateBarcode from './Dialog'
import { getListDetailApi as getDetailApi, getDeliveryReceiptMaterialListApi, updateListApi } from '@/apis/modules/in-storage/purchase/delivery'
import moment from 'moment'
moment.locale('zh-cn')

const INIT_FORM = () => ({
  no: '', // 交接单号
  contractNo: '', // 合同编号
  orderNo: '', // 订单号
  supplierName: '', // 供应商
  materialsUnit: '', // 用料单位
  deliveryDate: '', // 交货日期
  inspect: true, // 是否检验
  orderType: '', // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
  remarks: '', // 备注
  status: null // 交接单状态
})

export default {
  name: 'InStorageConsignDeliveryDetail',
  components: {
    GenerateBarcode
  },
  mixins: [paginationPage, printTagWired, tableHeadSet],
  data() {
    return {
      loading: false,
      tableLoading: false, // 表格loading
      query: {},
      tableData: [],
      tableSelection: [], // 选中物料列表
      refreshKey: 0,
      refTableName: 'baseTableRef',
      columnSets: [],
      expandList: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      params: {
        view: '', // 页面类型
        orderId: '', // 订单id
        id: '', // 交接单id
        type: '', // 订单类型
        routeName: '' // 来源路由名称
      },
      form: INIT_FORM(),
      rules: {
        deliveryDate: [
          { required: true, message: '此项必填', trigger: 'blur' }
        ],
        inspect: [
          { required: true, message: '此项必填', trigger: 'blur' }
        ]
      },
      currentRow: null
    }
  },
  computed: {
  },
  watch: {
  },
  mounted() {
    this.params = this.$route.params
    this.initBaseInfo().then(() => this.getList()) // 获取交接单信息并填充(里头有订单类型orderType字段)
  },
  methods: {
    // 基础交接单信息获取
    initBaseInfo() {
      this.loading = true
      // 以下两个入参二选一，当新增交接单时，则需要传入订单ID，初始化部分数据，编辑交接单时传入当前交接单ID，不可两个都传入
      const params = {
        deliveryReceiptId: this.params.id
      }
      // 查看交接单详情
      return new Promise((resolve, reject) => {
        getDetailApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            this.form = { ...data }
            // 凭证日期，设置缺省日期
            this.form.deliveryDate = data.deliveryDate || moment().format('YYYY-MM-DD')
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 调用以更新交接单信息(废弃)
    initAutoUpdate() {
      const params = {
        ...this.form
      }
      return new Promise((resolve, reject) => {
        updateListApi(params).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 列表刷新会调用这个方法
    init() {
      this.getList()
    },
    // 生成条码弹窗确认回调
    async confirmCallBack() {
      await this.getList()
      const i = this.tableData.findIndex(c => c.id === this.dialogForm.id)
      if (i !== -1) {
        this.dialogForm = this.tableData[i]
      }
    },
    // 交接单物料列表获取
    getList() {
      this.tableLoading = true
      // 接口参数
      const params = {
        page: {
          ...this.pageInfo,
          size: 1000
        },
        ...this.query,
        orderId: null, // 采购订单id
        deliveryReceiptId: this.params.id // 交接单主键ID
      }
      return new Promise((resolve, reject) => {
        getDeliveryReceiptMaterialListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            // 删除未到货数量
            const lo = this.columnList.findIndex(item => !item.prop)
            this.columnList.splice(lo, 1)
            // 删除订单数量
            const lo1 = this.columnList.findIndex(item => item.prop === 'orderNumber')
            this.columnList.splice(lo1, 1)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.tableLoading = false
          resolve(res)
        }).catch(err => {
          this.tableLoading = false
          reject(err)
        })
      })
    },
    // 表格行内点击回调
    handleRowClick({ row, column, cell, event }) {
      this.currentRow = row
      if (column.label === '操作') {
        return false
      }
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 关闭页面
    handleGoBack() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: this.params.routeName,
        params: this.$route.query.extraInfo
      })
    },
    // 查看条码
    handleViewBarcode(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    // 表格一键全选改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 点击哪行高亮哪行
    handleCellStyle({ row }) {
      const cellStyle = {
        backgroundColor: row === this.currentRow ? '#409EFF' : ''
      }
      return cellStyle
    },
    // 控制表格某行是否可被勾选
    selectable(row, index) {
      return false
    },
    getDynamicRow(item) {
    },
    // 入库
    handleInWarehouse(item) {
      this.$router.push({
        name: 'InStorageInfoAdd',
        params: {
          id: this.params.id // 交接单id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-form-item__content {
    line-height: 0;
}
::v-deep .my-inspect-sty {
  .el-radio-button__orig-radio:disabled+.el-radio-button__inner {
    color: #606266;
  }
}
</style>
