<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="75%"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div style="margin: 0 10px;">
      <divider>交接物料</divider>
      <el-row>
        <el-descriptions :border="true" direction="vertical" :column="8">
          <el-descriptions-item label="物料编码">{{ dialogForm.materialCode }}</el-descriptions-item>
          <el-descriptions-item label="物料名称">{{ dialogForm.materialName }}</el-descriptions-item>
          <el-descriptions-item label="规格型号">{{ dialogForm.materialSpec }}</el-descriptions-item>
          <el-descriptions-item label="评估类型">{{ dialogForm.materialAssessType_dictText }}</el-descriptions-item>
          <el-descriptions-item label="订单数量">{{ dialogForm.orderNumber }}</el-descriptions-item>
          <el-descriptions-item label="计量单位">{{ dialogForm.unitName }}</el-descriptions-item>
          <el-descriptions-item label="已到货数量">{{ dialogForm.deliveredNumber }}</el-descriptions-item>
          <el-descriptions-item label="本次到货数量">{{ dialogForm.deliveryNumber }}</el-descriptions-item>
        </el-descriptions>
      </el-row>

      <divider>条码信息</divider>
      <el-row style="margin: 10px 0;">
        <el-button type="primary" :disabled="tableData.length === 0" @click="handlePrintAll">打印全部条码</el-button>
      </el-row>
      <table-com
        :key="refreshKey"
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :table-height-type="380"
        :operation-width="120"
        :expand-list="expandList"
        :table-column-sort="false"
        :show-operate="true"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handlePrint([scope.row.id])">打印条码</el-button>
        </template>
      </table-com>
    </div>

  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import printTagWired from '@/mixins/printTagWired'
import { getBottomDetailListApi as getListApi, printApi } from '@/apis/modules/in-storage/purchase/delivery'

export default {
  name: 'InStorageConsignCom',
  components: {
  },
  mixins: [paginationPage, printTagWired],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => ({})
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: '物料明细及条码',
      refreshKey: new Date().getTime(),
      loading: false,
      rules: {},
      query: {},
      tableData: [],
      columnList: [],
      columnListBase: [
        {
          module: 'word',
          prop: 'batchNo',
          label: '批次号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'qualityGuaranteePeriod',
          label: '到期时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (item) => {
            // Tip expirationDate 是否存在到期时间 枚举值0/1
            return !item.expirationDate ? '无' : item.qualityGuaranteePeriod
          },
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '打印次数',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnListExtra: [
        {
          module: 'word',
          prop: 'batchNo',
          label: '批次号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'qualityGuaranteePeriod',
          label: '到期时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (item) => {
            // Tip expirationDate 是否存在到期时间 枚举值0/1
            return !item.expirationDate ? '无' : item.qualityGuaranteePeriod
          },
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'printNumber',
          label: '打印次数',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      expandList: []
    }
  },
  computed: {
  },
  watch: {
    dialogForm: {
      handler(item) {
        this.$nextTick(() => {
          this.columnList = (item.useMinUnit && item.unitName2 === '序列号') ? this.columnListExtra : this.columnListBase
        })
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...this.query,
          deliveryMaterialId: this.dialogForm.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 打印全部条码
    handlePrintAll() {
      const ids = this.tableData.map(i => i.id)
      this.handlePrint(ids)
    },
    // 打印单条条码
    handlePrint(ids) {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      return new Promise((resolve, reject) => {
        printApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.device && data.length && this.$notify.success({
              title: '成功',
              message: data.length > 30 ? `打印全部条码比较耗时，请耐心等待` : `已开始打印`
            })
            this.device && data.length && data.forEach(item => this.print(item))
            this.init()
          }
          loading.close()
          resolve(res)
        }).catch(err => {
          loading.close()
          reject(err)
        })
      })
    },
    // 删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.tableData = this.tableData.filter(i => i.uuid !== item.uuid)
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.$nextTick(() => {
        this.columnList = (this.dialogForm.useMinUnit && this.dialogForm.unitName2 === '序列号') ? this.columnListExtra : this.columnListBase
        this.init()
      })
    },
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  ::v-deep .el-drawer__header {
    margin-bottom: 10px !important;
  }

  .drawer-footer{
    margin-top: 10px;
    margin-right: 30px;
  }
  </style>

